#pageHeading  {
    display: grid;
    place-content: center;
    margin-top: 10%;
}

.circular-landscape {
    display: inline-block;
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
  }
  
  .circular-landscape img {
    width: auto;
    height: 100%;
    margin-left: -50px;
  }